* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: fit-content;
    background-color: #87B5B5;
}

textarea {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 15px;
    background-color: #C1D9D6;
    margin-bottom: 0;
}

#main {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    margin-top: 20px;
}

#text-display {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    height: fit-content;
    min-height: 500px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 12px 12px rgba(0, 0, 0, 0.3);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

#editor-div {
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    height: fit-content;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 12px 12px rgba(0, 0, 0, 0.3);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    margin-bottom: 20px;
    padding-bottom: 0;
}

#editor-div-top {
    width: 100%;
    background-color: #4EA69D;
    height: 50px;
    padding-left: 20px;
    display: flex;
    align-items: center;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 12px 12px rgba(0, 0, 0, 0.3);
}

#editor-div-top p {
    margin-right: auto;
    color: white;
    font-size: 20px;
}

#preview-top {
    margin-bottom: auto;
    width: 100%;
    background-color: #4EA69D;
    height: 50px;
    padding-left: 20px;
    display: flex;
    align-items: center;
    color: white;
    font-size: 20px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 12px 12px rgba(0, 0, 0, 0.3);
}

#preview {
    width: 100%;
    padding: 30px;
    height: 500px;
    background-color: #C1D9D6;
    overflow: scroll;
}

#preview p {
    margin-bottom: 7px;
}

pre {
    background-color: whitesmoke;
    margin-top: 13px;
    margin-bottom: 13px;
}

code {
    background-color: whitesmoke;
}

h1 {
    border-bottom: 2px solid black;
    margin-bottom: 20px;
}

h2 {
    border-bottom: 1px solid black;
    margin-bottom: 15px;
}

h3 {
    margin-bottom: 12px;
}

img {
    width: 80%;
    margin-top: 15px;
    margin-bottom: 15px;
}

ol {
    margin-top: 20px;
    margin-bottom: 20px;
}

ul {
    margin-top: 20px;
    margin-bottom: 20px;
}

table {
    margin-top: 20px;
    margin-bottom: 20px;
    border: 0.5px solid black;
}

th {
    border: 0.5px solid black;
    padding: 3px;
}

td {
    border: 0.5px solid black;
    padding: 3px;
}

blockquote {
    margin-top: 7px;
    margin-bottom: 15px;
}

